var server = window.location.hostname;
var port = "1337";
var Graph = /** @class */ (function () {
    function Graph(vector, id_graph) {
        this.vector = vector;
        this.id_graph = id_graph;
    }
    return Graph;
}());
var add_button = document.getElementById('add');
var add_form = document.getElementById('add_form');
var execute_operation = document.getElementById('execute_operation');
var target = document.getElementById('target');
var forms = document.getElementById('forms');
var status_request = document.getElementById("status_request");
var vector = document.getElementById("vector");
var operation_form = document.getElementById("operation");
var count_input = 0;
function getValueAndConvertToNumber() {
    var vectors = [];
    for (var _i = 0, _a = Array.prototype.slice.call(forms.getElementsByTagName("form")); _i < _a.length; _i++) {
        var item = _a[_i];
        var vector = [];
        for (var _b = 0, _c = item.getElementsByTagName("input"); _b < _c.length; _b++) {
            var value = _c[_b];
            vector.push(Number(value.value));
        }
        vectors.push(vector);
    }
    return vectors;
}
function getOperation() {
    return parseInt(operation_form.value);
    // var operation: number = operation_form
    // console.log(operation)
    // return operation
}
function addElement() {
    for (var _i = 0, _a = Array.prototype.slice.call(forms.getElementsByTagName("form")); _i < _a.length; _i++) {
        var item = _a[_i];
        item.insertAdjacentHTML('beforeend', "<input id=" + item.getElementsByTagName('input').length + ">");
    }
    count_input += 1;
}
function addElementForm() {
    var value_form = "array" + forms.getElementsByTagName('form').length + "[]";
    forms.insertAdjacentHTML('beforeend', "<form name=" + value_form + " id=\"" + value_form + "\">");
    var get_new_form = document.getElementById("" + value_form);
    for (var i = 1; i <= count_input; i++) {
        get_new_form.insertAdjacentHTML('beforeend', "<input id=" + i + ">");
    }
}
function createVector() {
    var vectors = getValueAndConvertToNumber();
    var operation = getOperation();
    var obj = { vectors: vectors, operation: operation };
    console.log(obj);
    fetch("http://" + server + ":" + port + "/operation/", { method: "POST", headers: {
            'Accept': 'application/json, text/plain',
            'Content-Type': 'application/json;charset=UTF-8'
        }, body: JSON.stringify(obj) })
        .then(function (response) {
        console.log(response);
        return response.json().then(function (data) { return ({ status: response.status, body: data }); });
    }).then(function (data) {
        if (data.status == 200) {
            status_request.innerHTML = "";
            vector.innerHTML = data.body.vector;
        }
        else {
            status_request.innerHTML = "Did not work out";
        }
    });
}
add_button.addEventListener('click', addElement);
execute_operation.addEventListener('click', createVector);
add_form.addEventListener('click', addElementForm);
